<template>
  <div>
    <v-card max-width="1000px" class="mx-auto mt-5">
      <v-data-table
        :headers="headers"
        :items="items"
        :hide-default-footer="true"
        disable-pagination
        disable-filtering
        disable-sort
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Matérias</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                  :disabled="locked"
                >
                  Nova matéria
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-data-table
                      v-if="showDefaultSubjects"
                      v-model="selectedDefaultSubjects"
                      :items="defaultSubjects"
                      :headers="defaultSubjectHeaders"
                      item-key="nome_materia_padrao"
                      show-select
                      hide-default-footer
                    >
                      <template v-slot:top>
                        <v-toolbar flat>
                          <v-toolbar-title
                            >Acrescentar Matérias Padrão</v-toolbar-title
                          >
                        </v-toolbar>
                      </template>
                    </v-data-table>

                    <v-form v-else @submit.prevent ref="form">
                      <v-row>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            v-model="editedItem.name"
                            label="Nome da matéria"
                            :rules="[rules.required]"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-form>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancelar
                  </v-btn>
                  <v-btn
                    v-if="defaultSubjects.length"
                    color="blue darken-1"
                    text
                    @click="showDefaultSubjects = !showDefaultSubjects"
                    >{{
                      showDefaultSubjects
                        ? "Criar uma matéria diferente"
                        : "Ver matérias padrão"
                    }}</v-btn
                  >
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="save"
                    :disabled="
                      showDefaultSubjects && !selectedDefaultSubjects.length
                    "
                  >
                    OK
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5"
                  >Tem certeza que deseja excluir essa matéria?
                </v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >Cancelar</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >OK</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <!-- eslint-disable-next-line -->
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)" :disabled="locked">
            mdi-pencil
          </v-icon>
          <v-icon small @click="deleteItem(item)" :disabled="locked">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
import validationMixin from "@/mixins/validation";

export default {
  name: "TabelaMatérias",
  props: ["items", "locked", "materias"],
  mixins: [validationMixin],
  data() {
    return {
      headers: [
        { text: "Nome", value: "name" },
        { text: "Ações", value: "actions", sortable: false, width: "10%" },
      ],
      defaultSubjectHeaders: [{ text: "Nome", value: "nome_materia_padrao" }],
      selectedDefaultSubjects: [],
      defaultSubjects: [],
      showDefaultSubjects: true,
      editedIndex: -1,
      editedItem: {
        name: "",
      },
      defaultItem: {
        name: "",
      },
      dialogDelete: false,
      dialog: false,
      formTitle: null,
    };
  },

  watch: {
    dialog(val) {
      val || this.close();

      if (val === true) {
        this.onOpen();
      }
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  mounted() {
    this.loadDefaultSubjects();
  },

  methods: {
    onOpen() {
      this.resetValidation();
      this.selectedDefaultSubjects = [];
      if (!this.materias.length && this.defaultSubjects.length) {
        this.showDefaultSubjects = true;
      } else {
        this.showDefaultSubjects = false;
      }
    },

    async loadDefaultSubjects() {
      const resp = await this.$axios.get("/materias/padrao");
      const { materias } = resp.data;
      if (materias) {
        this.defaultSubjects = materias;
      }
    },

    editItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
      this.showDefaultSubjects = false;
    },

    deleteItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$emit("delete", this.editedIndex);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    subjectExists(subject) {
      return (
        this.materias != null &&
        this.materias.length &&
        this.materias.find(
          (materia) => materia.name.trim() === subject.name.trim()
        ) != null
      );
    },

    save() {
      if (this.showDefaultSubjects) {
        if (!this.selectedDefaultSubjects.length) {
          return;
        }
        for (const defaultSubject of this.selectedDefaultSubjects) {
          const item = { name: defaultSubject.nome_materia_padrao };
          const editedIndex = -1;
          if (!this.subjectExists(item)) {
            this.$emit("save", editedIndex, item);
          }
        }
        this.close();
      } else {
        if (!this.validate()) {
          return;
        }

        if (this.subjectExists(this.editedItem)) {
          return this.$toast.error("Já existe outra matéria com esse nome");
        }

        this.$emit("save", this.editedIndex, this.editedItem);
        this.close();
      }
    },
  },
};
</script>

